import { Link as RouterLink } from "react-router-dom";

import {
    Box,
    Grid,
    Typography,
    Link,
    Avatar,
    Button,
    Stack,
    Tooltip,
    IconButton,
    styled,
    alpha,
    Accordion,
    AccordionSummary,
    AccordionDetails,
} from "@mui/material";
import { shadows } from '@mui/system';

import PeopleIcon from "@mui/icons-material/People";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import HomeIcon from "@mui/icons-material/Home";
import LogoutIcon from "@mui/icons-material/Logout";
import SettingsIcon from "@mui/icons-material/Settings";
import avatarSrc from "../../../assets/avatar.jpg";
import HighlightAltIcon from '@mui/icons-material/HighlightAlt';
import LocalGroceryStoreOutlinedIcon from '@mui/icons-material/LocalGroceryStoreOutlined';

const Sidebar = styled("div")(({ theme }) => ({
    backgroundColor: theme.palette.info.dark,
    color: theme.palette.grey[50],
    width: "15rem",
    padding: theme.spacing(2, 2),
    position: "absolute",
    left: "0",
    minHeight: '90.9vh'
}));

const ButtonItem = styled(Button)(({ theme }) => ({
    width: "100%",
    "&:hover": {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
        color: "white",
    },
   
    marginBottom: theme.spacing(1),
    "&:active": {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
        color: "white",
    },
    wrap: "nowrap",
    fontSize: "0.9rem",
    textTransform: "capitalize",
}));

const Item = styled("div")(({ theme }) => ({
    display: "flex",
    alginItem: "flex-left",
    gap: theme.spacing(2),
    padding: theme.spacing(1, 2),
    marginBottom: theme.spacing(1),
  
 
    borderRadius: theme.shape.borderRadius,
    "&:hover": {
        backgroundColor: alpha(theme.palette.common.white, 0.15),
        boxShadow: theme.shadows[3],
    },
    cursor: 'pointer',
    
}));

const AdminSidebar = () => {
    return (
        <Sidebar>
            <Stack sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}>
                <Avatar alt="avatar" src={avatarSrc} sx={{ width: 56, height: 56 }} />
                <Typography variant="h6">Uy Dng</Typography>
                <Typography variant="caption">Administrator</Typography>
                <Stack direction="row" justifyContent="flex-start" alignItems="flex-start">
                    <Tooltip title="Go to Homepage" color="secondary">
                        <IconButton aria-lable="Go to home page">
                            <Link href="/">
                                <HomeIcon />
                            </Link>
                        </IconButton>
                    </Tooltip>
                    <Tooltip title="Setting account">
                        <IconButton aria-label="Setting account" color="secondary">
                            <Link href="#">
                                <SettingsIcon />
                            </Link>
                        </IconButton>
                    </Tooltip>

                    <Tooltip title="Logout">
                        <IconButton aria-label="Logout" color="secondary">
                            <Link href="#">
                                <LogoutIcon />
                            </Link>
                        </IconButton>
                    </Tooltip>
                </Stack>
            </Stack>

            <Stack sx={{marginTop: '2rem'}}>
                <Item  >
                    <PeopleIcon />
                    <Typography>Customer</Typography>
                </Item>

                <Item>
                    <LocalGroceryStoreOutlinedIcon />
                    <Typography>Cart Store</Typography>
                </Item>

                <Item>
                    <HighlightAltIcon />
                    <Typography>Products</Typography>
                </Item>
            </Stack>
        </Sidebar>
    );
};

export default AdminSidebar;
