import { Link as RouterLink } from "@mui/material";
import {
    Box,
    Typography,
    Menu,
    MenuItem,
    AppBar,
    Tooltip,
    Toolbar,
    IconButton,
    Badge,
    styled,
    alpha,
    InputBase,
    Link,
} from "@mui/material";
import AccountCircle from "@mui/icons-material/AccountCircle";
import MailIcon from "@mui/icons-material/Mail";
import MenuIcon from "@mui/icons-material/Menu";
import NotificationsIcon from "@mui/icons-material/Notifications";
import MoreIcon from "@mui/icons-material/MoreVert";
import SearchIcon from "@mui/icons-material/Search";
import { useState } from "react";

const SearchBox = styled("div")(({ theme }) => ({
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    "&:hover": {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
 
    marginRight: theme.spacing(2),
    width: "100%",
    [theme.breakpoints.up("sm")]: {
        marginLeft: theme.spacing(0),
        width: "auto",
    },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
    position: "absolute",
    height: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    padding: theme.spacing(0, 2),
    pointerEvents: "none",
}));

const InputBaseBox = styled(InputBase)(({ theme }) => ({
    color: "inherit",

    "& .MuiInputBase-input": {
        padding: theme.spacing(1, 1, 1, 0),
        paddingLeft: `calc(1em + ${theme.spacing(4)})`,
        transition: theme.transitions.create("width"),
        width: "100%",
        [theme.breakpoints.up("md")]: {
            width: "20ch",
        },
    },
}));

const AdminHeader = () => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [mobileMenuAnchorEl, setMobileMenuAnchorEl] = useState(null);

    const isProfileOpen = Boolean(anchorEl);
    const isMobileMenuOpen = Boolean(mobileMenuAnchorEl);

    const profileMenuOpenHandler = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const profileMenuCloseHandler = () => {
        setAnchorEl(null);
    };

    const mobileMenuOpenHandler = (event) => {
        setMobileMenuAnchorEl(event.currentTarget);
    };

    const mobileMenuCloseHandler = () => {
        setMobileMenuAnchorEl(null);
        profileMenuCloseHandler();
    };

    const menuId = "primary-profile-account-menu";
    const renderProfileMenu = (
        <Menu
            anchorEl={anchorEl}
            anchorOrigin={{
                vertical: "top",
                horizontal: "right",
            }}
            open={isProfileOpen}
            onClose={profileMenuCloseHandler}
            keepMounted
            transformOrigin={{
                vertical: "top",
                horizontal: "right",
            }}
            id={menuId}
        >
            <MenuItem onClick={profileMenuCloseHandler}>Your Profile</MenuItem>
            <MenuItem onClick={profileMenuCloseHandler}>Your account</MenuItem>
        </Menu>
    );

    const mobileMenuId = "primary-mail-notification-profile-mobile-menu";
    const renderMobileMenu = (
        <Menu
            anchorEl={mobileMenuAnchorEl}
            anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
            id={mobileMenuId}
            open={isMobileMenuOpen}
            onClose={mobileMenuCloseHandler}
            transformOrigin={{ vertical: "top", horizontal: "right" }}
            keepMounted
        >
            <MenuItem>
                <IconButton>
                    <Badge badgeContent={1}>
                        <MailIcon />
                    </Badge>
                </IconButton>
                <p>Mail Box</p>
            </MenuItem>

            <MenuItem>
                <IconButton>
                    <Badge badgeContent={18}>
                        <NotificationsIcon />
                    </Badge>
                </IconButton>
                <p>Notifications</p>
            </MenuItem>

            <MenuItem onClick={profileMenuOpenHandler}>
                <IconButton>
                    <Badge badgeContent={18}>
                        <NotificationsIcon />
                    </Badge>
                </IconButton>
                <p>Your account</p>
            </MenuItem>
        </Menu>
    );

    return (
        <Box  >
            <AppBar sx={{  position: 'sticky', top: '0', right: '0'}}>
                <Toolbar  >
                    <IconButton color="inherit" size="large" sx={{ mr: 2, display: { xs: "block", md: "none" } }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" sx={{ mr: 2, display: { xs: "none", md: "block" } }} noWrap>
                <Link color="inherit" underline="hover" component={RouterLink} to="/">
                    Admin Dashboard
                </Link>
            </Typography>
                   

                    <SearchBox>
                        <SearchIconWrapper>
                            <SearchIcon />
                        </SearchIconWrapper>
                        <InputBaseBox placeholder="Search..." inputProps={{ "aria-label": "Search" }} />
                    </SearchBox>

                    <Box sx={{ flexGrow: 1 }} />
                    <Box sx={{ display: { xs: "none", md: "flex" } }}>
                        <Tooltip title="MailBox">
                            <IconButton size="large" color="inherit">
                                <Badge badgeContent={8} color="error">
                                    <MailIcon />
                                </Badge>
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Notifications">
                            <IconButton size="large" color="inherit" aria-label="show notifications">
                                <Badge badgeContent={18} color="error">
                                    <NotificationsIcon />
                                </Badge>
                            </IconButton>
                        </Tooltip>

                        <Tooltip title="My account">
                            <IconButton size="large" color="inherit" onClick={profileMenuOpenHandler}>
                                <AccountCircle />
                            </IconButton>
                        </Tooltip>
                    </Box>

                    <Box sx={{ display: { xs: "block", md: "none" } }}>
                        <IconButton
                            color="inherit"
                            size="large"
                            aria-label="show more"
                            onClick={mobileMenuOpenHandler}
                        >
                            <MoreIcon />
                        </IconButton>
                    </Box>
                </Toolbar>
            </AppBar>
            {renderProfileMenu}
            {renderMobileMenu}
        </Box>
    );
};

export default AdminHeader;
